import React from 'react'
import './StartPage.css'
import 'react-dom'

function StartPage(props){
    return (
     <div className = "title">
     <h1 className='fadeIn first'>Welcome to the Game of Life!!!</h1>
     <h2 className='fadeIn second'>Because just like your life, this game is pointless and serves no purpose.</h2>
     <h3 className='fadeIn third'>Please select the level according to how worthless you consider your time.</h3>
     <select className='fadeIn third' id="gridsize">
        <option value="2">2</option>
        <option value="3">3</option>
        <option value="4">4</option>
    </select>
    <br></br>
    <input className='fadeIn third' id="levelselect" type="submit" value="Start" onClick = {props.onClick}/>
    </div>
    
    )
}

export default StartPage;